




.half {

  padding: 0 1em;
}


.tab {
  position: relative;
  margin-bottom: 1px;
  width: 100%;
  color: #fff;
  overflow: hidden;
}

.acor-input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

label {
  height: 100%;
  position: relative;
  display: block;
  padding: 0 0 0 1em;
  background-color: #000;
  border: 1px solid #ffffff;
  box-shadow: inset 0 0 5px red,0 0 5px red  ;
  font-weight: bold;
  line-height: 3;
  cursor: pointer;
}

.blue label {
  background: #ddc3ab;
}

.tab-content {
  max-height: 0;
  overflow: hidden;
  background: #ffff;
  color: #000;
  -webkit-transition: max-height .35s;
  -o-transition: max-height .35s;
  transition: max-height .35s;
}

.blue .tab-content {
  background: #3498db;
}

.tab-content p {
  margin: 1em;
}

/* checked */
input:checked~.tab-content {
  max-height: 10em;
}

/* Icon */
label::after {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 3em;
  height: 3em;
  line-height: 3;
  text-align: center;
  -webkit-transition: all .35s;
  -o-transition: all .35s;
  transition: all .35s;
}

input[type=checkbox]+label::after {
  content: "+";
}

input[type=radio]+label::after {
  content: "\25BC";
}

input[type=checkbox]:checked+label::after {
  transform: rotate(315deg);
}

input[type=radio]:checked+label::after {
  transform: rotateX(180deg);
}


